@import url(https://fonts.googleapis.com/css?family=Roboto:300);
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:300);
header h1 {
  background-color: #de3f53;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  color: #fff;
  font-family: 'Roboto Condensed', Arial, sans-serif;
  font-size: 20px;
  font-weight: 100;
  line-height: 56px;
  padding-left: 15px;
  margin: 0;
  text-transform: uppercase;
}

header nav {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
}

header .link {
  background: transparent;
  display: inline-block;
  vertical-align: top;
  width: 100px;
  height: 34px;
  line-height: 36px;
  text-transform: uppercase;
  text-decoration: none;
  color: #333;
  letter-spacing: 0.1em;
  text-align: center;
  font-size: 0.8rem;
  margin: 10px;
  position: relative;
  border: 2px solid transparent;
}

header .link::after {
  width: 0%;
  height: 2px;
  display: block;
  background-color: #333;
  content: ' ';
  position: absolute;
  top: 34px;
  left: 50%;
  transition: left 0.2s cubic-bezier(0.215, 0.61, 0.355, 1),
    width 0.2s cubic-bezier(0.215, 0.61, 0.355, 1);
}
header .link:hover::after {
  width: 100%;
  height: 2px;
  display: block;
  background-color: #333;
  content: ' ';
  position: absolute;
  top: 34px;
  left: 0;
}

.addTodo {
  display: flex;
  width: 100%;
}

.addTodo input[type='text'] {
  background-color: #171d37;
  border: none;
  box-shadow: inset 0 0 8px 0 #0e1329;
  color: #464f72;
  font-size: 14px;
  margin: 0;
  width: 100%;
  padding: 20px 15px;
  transition: all 0.2s linear;
}

.addTodo input[type='text']:hover {
  color: #4c577f;
}

.addTodo input[type='text']:focus {
  color: #b1b2c9;
}

.addTodo input[type='submit'] {
  background-color: #171d37;
  border: none;
  box-shadow: inset 0 0 8px 0 #0e1329;
  color: #464f72;
  font-size: 14px;
  margin: 0;
  padding: 20px 35px;
  transition: all 0.2s linear;
}

.addTodo input[type='submit']:hover {
  color: #5e6a99;
}

.addTodo ::-webkit-input-placeholder {
  color: #464f72;
} /* WebKit browsers */
.addTodo :-moz-placeholder {
  color: #464f72;
} /* Mozilla Firefox 4 to 18 */
.addTodo ::-moz-placeholder {
  color: #464f72;
} /* Mozilla Firefox 19+ */
.addTodo :-ms-input-placeholder {
  color: #464f72;
} /* Internet Explorer 10+ */

ul {
  list-style: none;
  margin: 0px auto;
  width: 100%;
  padding: 0;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

li {
  background-color: #262e4c;
  border-bottom: 1px solid #1c2340;
  color: #b1b2c9;
}

li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

li span {
  margin-left: 30px;
  transition: all 0.2s linear;
}

label {
  cursor: pointer;
  display: block;
  line-height: 56px;
  padding: 0 15px;
  position: relative;
}

label:hover {
  background-color: #2a3353;
  color: #8284a3;
}

label button {
  border: none;
  padding: 0;
  background-color: #de3f53;
  border-radius: 50%;
  color: #fff;
  display: none;
  float: right;
  font-weight: bold;
  line-height: normal;
  height: 16px;
  margin-top: 20px;
  text-align: center;
  text-decoration: none;
  width: 16px;
  transition: all 0.2s linear;
}

label:hover button {
  display: block;
}

label button:hover {
  background-color: #fff;
  color: #de3f53;
}

input[type='checkbox'] {
  cursor: pointer;
  opacity: 0;
  position: absolute;
}

input[type='checkbox'] + i {
  background-color: #404a6e;
  border-radius: 50%;
  display: block;
  height: 16px;
  position: absolute;
  top: 20px;
  width: 16px;
  z-index: 1;
}

input[type='checkbox']:checked + i::after {
  background-color: #6e6e96;
  border-radius: 50%;
  content: '';
  display: block;
  height: 8px;
  left: 4px;
  position: absolute;
  top: 4px;
  width: 8px;
  z-index: 2;
}

input[type='checkbox']:checked ~ span {
  color: #586186;
  text-decoration: line-through;
}

li.remove {
  -webkit-animation: collapseItem 300ms ease;
  animation: collapseItem 300ms ease;
  transform-origin: 50% 0%;
}

li.remove span {
  color: #586186;
  text-decoration: line-through;
}

@-webkit-keyframes collapseItem {
  0% {
    transform: perspective(500px) rotateX(0deg);
  }
  100% {
    transform: perspective(500px) rotateX(-90deg);
  }
}

@keyframes collapseItem {
  0% {
    transform: perspective(500px) rotateX(0deg);
  }
  100% {
    transform: perspective(500px) rotateX(-90deg);
  }
}

.confirmation__data {
  text-align: left;
  width: 100%;
  padding: 20px;
  color: #b1b2c9;
  background: #262e4c;
  font-size: 20px;
}

.confirmation__data:first-of-type {
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}

.confirmation__data:last-of-type {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.confirmation__label {
  text-transform: uppercase;
  font-weight: bold;
  margin: 10px;
}

.confirmation__label::after {
  content: ':';
}

.login {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.login input {
  background-color: #171d37;
  border: none;
  box-shadow: inset 0 0 8px 0 #0e1329;
  color: #464f72;
  font-size: 14px;
  margin: 0;
  width: 100%;
  padding: 20px 15px;
  transition: all 0.2s linear;
}

.login input:hover {
  color: #4c577f;
}

.login input:focus {
  color: #b1b2c9;
}

.login button,
.login .link {
  background-color: #de3f53;
  border: none;
  box-shadow: inset 0 0 8px 0 #b4293c;
  color: #e08c97;
  font-size: 14px;
  margin: 20px;
  padding: 20px 35px;
  border-radius: 4px;
  transition: all 0.2s linear;
  text-decoration: none;
}

.login button:hover,
.login .link:hover {
  color: #fff;
}

.login ::-webkit-input-placeholder {
  color: #464f72;
} /* WebKit browsers */
.login :-moz-placeholder {
  color: #464f72;
} /* Mozilla Firefox 4 to 18 */
.login ::-moz-placeholder {
  color: #464f72;
} /* Mozilla Firefox 19+ */
.login :-ms-input-placeholder {
  color: #464f72;
} /* Internet Explorer 10+ */

.App {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.container {
  width: 500px;
  margin: 40px 20px 10px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a,
button {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


